<template>
  <div id="app">
        <keep-alive>
           <router-view name="i"></router-view>
        </keep-alive>
      <keep-alive>
        <router-view name="h"></router-view>
      </keep-alive>
  </div>
</template>

<script>
export default {
  name: 'app',
  data(){
    return {
    }
  },
   mounted(){
    //  this.$router.push({path:'/indexinfo'});
    this.$router.push({path:'/indexinfo/indexAll',query:{'type':'All'}});

   },
  watch: {
  '$route' (to, from) {
    }
  }
}
</script>

<style lang="scss" scoped>
@charset "utf-8";
@function pxx($px, $base-font-size: 75px) {
  @if (unitless($px)) {
    @return pxx($px + 0px); // That may fail.
  }
  @else if (unit($px)==rem) {
    @return $px;
  }
  @return ($px / $base-font-size) * 1rem;
}

@mixin font-dpr($font-size) {
  font-size: $font-size / 2;

  [data-dpr="2"] & {
    font-size: $font-size;
  }

  [data-dpr="3"] & {
    font-size: $font-size * 1.5;
  }
}

@mixin px-dpr($property, $size, $others:"") {

  #{$property}: $size / 2 #{$others};

  [data-dpr="2"] & {
    #{$property}: $size #{$others};
  }

  [data-dpr="3"] & {
    #{$property}: $size * 1.5 #{$others};
  }
}

* {
  padding: 0;
  margin: 0;
}
html,body{
  height: 100%;
  width: 100%;
}
#app {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

</style>


