import Vue from 'vue'
import Router from 'vue-router'
import indexinfo from './../components/indexinfo.vue'
import indexdetails from './../components/indexdetails.vue'
import indexAll from './../components/index/indexAll.vue'
import indexShare from './../components/index/indexShare.vue'
import indexEssence from './../components/index/indexEssence.vue'
import indexQuestion from './../components/index/indexQuestion.vue'
import indexRecruit from './../components/index/indexRecruit.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    
    {
      path: '/indexinfo',
      name: 'indexinfo',
      components:{
       i: indexinfo
      },
      children:[
        {
      path: '/indexinfo/indexAll',
      name: 'indexAll',
      components:{
       a: indexAll
      }
    },
     {
      path: '/indexinfo/indexEssence',
      name: 'indexEssence',
      components:{
       b: indexEssence
      }
    },
     {
      path: '/indexinfo/indexShare',
      name: 'indexShare',
      components:{
       c: indexShare
      }
    },{
      path: '/indexinfo/indexQuestion',
      name: 'indexQuestion',
      components:{
       d: indexQuestion
      }
    },{
      path: '/indexinfo/indexRecruit',
      name: 'indexRecruit',
      components:{
       e: indexRecruit
      }
    }

      ]
    },
    {
      path: '/details',
      name: 'indexdetails',
      components: {
       h : indexdetails
      }
    }
  ],
  scrollBehavior (to, from, savedPosition) {
    // return 期望滚动到哪个的位置
    console.info(to,from,savedPosition);
    //  return { x: 0, y: 1000 }
    return savedPosition
  }
})



