<template>
    <div>
      <munav class="munav"></munav>
      <keep-alive>
         <router-view name="a"  ></router-view>
      </keep-alive>
       <keep-alive>
         <router-view name="b"  ></router-view>
      </keep-alive>
       <keep-alive>
         <router-view name="c"  ></router-view>
      </keep-alive>
       <keep-alive>
         <router-view name="d"  ></router-view>
      </keep-alive>
       <keep-alive>
         <router-view name="e"  ></router-view>
      </keep-alive>
    </div>
</template>

<script>
import munav from './index/munav'
export default {
  name: 'indexinfo',
  data(){
    return {
      type:''
    }
  },
  template: ['<munav/>'],
  components: { munav},
  mounted(){
    // this.$router.push({path:'/indexinfo/indexAll',query:{'type':'All'}});
  },
  watch: {
  '$route' (to, from) {
    console.info(to,from);
    this.type=to.query.type;
  }}
}
</script>

<style lang="scss" scoped>
@charset "utf-8";
@function pxx($px, $base-font-size: 75px) {
  @if (unitless($px)) {
    @return pxx($px + 0px); // That may fail.
  }
  @else if (unit($px)==rem) {
    @return $px;
  }
  @return ($px / $base-font-size) * 1rem;
}

@mixin font-dpr($font-size) {
  font-size: $font-size / 2;

  [data-dpr="2"] & {
    font-size: $font-size;
  }

  [data-dpr="3"] & {
    font-size: $font-size * 1.5;
  }
}

@mixin px-dpr($property, $size, $others:"") {

  #{$property}: $size / 2 #{$others};

  [data-dpr="2"] & {
    #{$property}: $size #{$others};
  }

  [data-dpr="3"] & {
    #{$property}: $size * 1.5 #{$others};
  }
}

* {
  padding: 0;
  margin: 0;
}
html,body{
  height: 100%;
  width: 100%;
}
#app {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

</style>


